import { Component, h, Prop, Element, Method } from '@stencil/core';
import ValidateInput from '../../input/validate';
declare var ksNewsletterToken: any;

import { commonDynamic, SubscriptionData } from "../../../global/data/commonDynamic";
import { DataLayer } from '../../../global/dataLayer';

@Component({
  tag: 'ks-newsletter-popup',
  styleUrl: 'newsletter-popup.css'
})
export class NewsletterPopup{
    @Element() root: HTMLElement;
    dialog: HTMLKsDialogElement;

    @Prop() api: string;
    @Prop() loginLink: string;
    @Prop() registerLink: string;

    @Prop() displayOnLoad: boolean = false;

    @Prop() loggedIn: boolean = false;
    @Prop({mutable: true}) subscribed: boolean = false;
    @Prop() email: string;

    @Prop() agreement: string = "Wyrażam zgodę na przetwarzanie danych osobowych do celów marketingowych, w celu zbadania opinii o sklepie oraz na otrzymywanie informacji handlowych na wskazany przeze mnie adres e-mail.";
    @Prop() infoMessage: string = "Aby otrzymać kupon rabatowy musisz posiadać konto w naszym sklepie. Minimalna wartość zamówienia wynosi: 100,00 zł";

    @Prop() successHeading: string = "ZAPISANO DO NEWSLETTERA";
    @Prop() successUnsubHeading: string = "WYPISANO Z NEWSLETTERA";
    @Prop() faliureHeading: string = "BŁĄD ZAPISU";

    success = false;

    componentDidLoad() {
        this.dialog = this.root.querySelector("ks-dialog");
        if(this.displayOnLoad && document.cookie.search("newsletterPopup=tak") == -1) {
            setTimeout(() => {
                this.Show();
            }, 4000);
        }
    }

    async requestHandler(event: Event) {
        event.preventDefault();
        if(!await ValidateInput(this.root.querySelector('form')))
            return;

        this.dialog.showLoading();

        const target = event.target as HTMLFormElement;
        const data = new FormData(target);

        if(this.subscribed)
        data.append("unsubscribe", "1");
        data.append("email", this.email);
        data.append("popup", "1");

        fetch(this.api, { body: data, method: "post"})
        .then(async response => {
            const result = await response.text();
            if(result.search("SUCCESS") != -1) {
                const heading = this.subscribed ? this.successUnsubHeading : this.successHeading;
                this.dialog.showSuccess(heading, result.replace("SUCCESS", ""));
                this.track();
                this.success = true;
            }
            else this.dialog.showFailure(this.faliureHeading, result);
        })
        .catch(async error => {
            let message = "";

            if(!window.navigator.onLine)
                message = "Brak internetu."

            if(error.message) message = error.message;

            this.dialog.showFailure(this.faliureHeading, message);
        });
    }

    track() {
        const data: SubscriptionData = {
            email: this.email,
            subscriber: !this.subscribed,
            place: "Zapis do newslettera | Popup"
        }

        DataLayer.subscribe(data);
    }

    setSubscription() {
        let customer = commonDynamic.get('customer');
        customer.subscriber = !this.subscribed;
        commonDynamic.set('customer', customer);
        this.subscribed = !this.subscribed;
    }
    

    @Method()
    async Show() {
        this.dialog.show();
    }

    SetCookie() {
        var expiration = "";
        var expirationDate = new Date();
        expirationDate.setMonth(expirationDate.getMonth() + 1);
        expiration = "expires=" + expirationDate.toUTCString() + "; ";

        document.cookie = "newsletterPopup=tak; " + expiration + "path=/";

        if(this.success) {
            this.setSubscription();
            this.success = false;
        }
    }
    

  render() {
    return [
        <ks-dialog dark smallmobile nopadding onClosed={()=>this.SetCookie()}>
            <form onSubmit={e => this.requestHandler(e)}>
                <div class="info">
                    <svg class="top" viewBox="0 0 303 15" width={909} height={45}>
                        <text x="0" y="12">Zapisz się do naszego Newslettera i zyskaj</text>
                    </svg>

                    <svg class="heading" viewBox="0 0 96 12" width={960} height={130}>
                        <text x="-1" y="12">KUPON 10ZŁ</text>
                    </svg>

                    <p>{this.infoMessage}</p>
                </div>
                    
                {!this.loggedIn ?
                    <div class="buttons">
                        <ks-button tall name="ZALOGUJ SIĘ" link={this.loginLink}></ks-button>
                        <ks-button tall name="NOWE KONTO" link={this.registerLink}></ks-button>
                    </div>
                : [
                    this.subscribed ? null : <ks-input-check large required nomessage name="zgoda" label={this.agreement} />,
                    <div class="email-form">
                        <div class="email">{this.email}</div>
                        <ks-button submit secondary={this.subscribed} name={this.subscribed ? "WYPISZ SIĘ" : "ZAPISZ SIĘ"} />
                    </div>
                ]}
            </form>
        </ks-dialog>
    ];
  }
}
